import AuthMethods from '@/store/auth/methods/auth.methods';
import SvgIcon from '@/components/utilities/SvgIcon.vue';
@Component({
    components: {
        SvgIcon,
    },
})
export default class ProtectedRouteExample extends Vue {
    async mounted() {
        await this.protectedRoute();
    }


    @auth.Action(AuthMethods.actions.PROTECTED_ROUTE)
    protectedRoute!:() => Promise<boolean>
}
